@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.home,
.reports,
.products,
.team,
.reports {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.home-container {
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, #ebebf0, rgba(0, 0, 0, 0.5));
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
  }
  
  .tab-nav {
    position: relative;
    height: 5%;
  }
  
  .tool-nav {
    height: 8%;
  }
  
  .tool-bar {
    height: 500%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding-left: 0px;
  }
  
  .action-btn {
    position: -webkit-sticky;
    position: sticky;
    top: 20px;
    height: 50px;
  }
  
  .action-btn + .action-btn {
    margin-left: 50px;
  }
  
  .home-body { 
    position: absolute;
  top: 50px; left: 400px;
    height: 0%;
  }
  
  .resources-action-card {
    height: auto;
    max-height: -webkit-fit-content;
    max-height: -moz-fit-content;
    max-height: fit-content;
    width: 500%;
  }
  
  .resources-container {
    height: 80vh;
    overflow-y: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  
  
  
  
  
  
  
