.home-container {
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, #ebebf0, rgba(0, 0, 255, 0.5));
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }
  
  .tab-nav {
    position: relative;
    height: 0%;
  }
  
  .tool-nav {
    height: 0%;
  }
  
  .tool-bar {
    height: 10%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding-left: 15px;
  }
  
  .action-btn {
    height: 50px;
    align-self: center;
    justify-self: center;
  }
  
  .action-btn + .action-btn {
    margin-left: 100px;
    align-self: center;
    justify-self: center;
  }
  
  .home-body { 
    position: absolute;
  top: 200px; left: 300px;
    height: 95%;
  }
  
  .resources-container {
    height: 80vh;
    overflow-y: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  

  

  